'use client';

import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { StockEntry } from '@/app/api/stock/route';
import { useLanguage } from '@/contexts/LanguageContext';

type StockManagerProps = {
  onStockUpdate?: () => void;
};

export default function StockManager({ onStockUpdate }: StockManagerProps) {
  const { t } = useLanguage();
  const [stock, setStock] = useState<StockEntry[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [editingStock, setEditingStock] = useState<StockEntry | null>(null);
  const [editForm, setEditForm] = useState({
    quantity: 0,
    lastUpdated: ''
  });

  // Fetch stock data
  useEffect(() => {
    fetchStock();
  }, []);

  const fetchStock = async () => {
    try {
      setIsLoading(true);
      const res = await fetch('/api/stock');
      if (!res.ok) throw new Error('Failed to fetch stock');
      const data = await res.json();
      setStock(data);
    } catch (error) {
      console.error('Error fetching stock:', error);
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditStock = (stockEntry: StockEntry) => {
    setEditingStock(stockEntry);
    setEditForm({
      quantity: stockEntry.quantity,
      lastUpdated: stockEntry.lastUpdated
    });
  };

  const handleSaveStock = async () => {
    if (!editingStock) return;

    try {
      const res = await fetch(`/api/stock?type=${editingStock.batteryType}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          batteryType: editingStock.batteryType,
          quantity: editForm.quantity,
          lastUpdated: editForm.lastUpdated
        })
      });

      if (!res.ok) {
        throw new Error('Failed to update stock');
      }

      await fetchStock();
      setEditingStock(null);
      onStockUpdate?.();
    } catch (error) {
      console.error('Error updating stock:', error);
    }
  };

  const getStockStatus = (quantity: number) => {
    if (quantity === 0) return { color: 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200', text: t('stock.outOfStock') };
    if (quantity < 5) return { color: 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200', text: t('stock.lowStock') };
    return { color: 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200', text: t('stock.inStock') };
  };

  if (isLoading) {
    return (
      <Card className="dark:bg-gray-800 dark:border-gray-700">
        <CardHeader>
          <CardTitle className="dark:text-white">{t('stock.title')}</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center py-8">
            <div className="text-gray-500 dark:text-gray-400">{t('common.loading')}</div>
          </div>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card className="dark:bg-gray-800 dark:border-gray-700">
      <CardHeader>
        <CardTitle className="dark:text-white">{t('stock.title')}</CardTitle>
        <p className="text-sm text-gray-600 dark:text-gray-400">
          {t('stock.description')}
        </p>
      </CardHeader>
      <CardContent>
        <div className="space-y-4">
          {stock.map((stockEntry) => {
            const status = getStockStatus(stockEntry.quantity);
            return (
              <div key={stockEntry.id} className="flex items-center justify-between p-4 border rounded-lg dark:border-gray-600 dark:bg-gray-700">
                <div className="flex items-center gap-4">
                  <Badge className={stockEntry.batteryType === 'AA' ? 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200' : 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200'}>
                    {stockEntry.batteryType}
                  </Badge>
                  <div>
                    <div className="font-medium dark:text-white">
                      {stockEntry.quantity} {stockEntry.quantity === 1 ? t('stock.battery') : t('stock.batteries')}
                    </div>
                    <div className="text-sm text-gray-500 dark:text-gray-400">
                      {t('stock.lastUpdated')}: {new Date(stockEntry.lastUpdated).toLocaleDateString('tr-TR', {
                        year: 'numeric',
                        month: 'short',
                        day: 'numeric'
                      })}
                    </div>
                  </div>
                </div>
                <div className="flex items-center gap-3">
                  <Badge className={status.color}>
                    {status.text}
                  </Badge>
                  <Dialog>
                    <DialogTrigger asChild>
                      <Button 
                        variant="outline" 
                        size="sm"
                        onClick={() => handleEditStock(stockEntry)}
                        className="dark:border-gray-600 dark:text-gray-300 dark:hover:bg-gray-600"
                      >
                        {t('stock.update')}
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="dark:bg-gray-800 dark:border-gray-700">
                      <DialogHeader>
                        <DialogTitle className="dark:text-white">
                          {t('stock.updateTitle')} - {stockEntry.batteryType}
                        </DialogTitle>
                      </DialogHeader>
                      <div className="space-y-4">
                        <div className="space-y-2">
                          <Label className="dark:text-gray-300">{t('stock.currentQuantity')}</Label>
                          <Input
                            type="number"
                            min="0"
                            value={editForm.quantity}
                            onChange={(e) => setEditForm({...editForm, quantity: parseInt(e.target.value) || 0})}
                            className="dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                          />
                        </div>
                        <div className="space-y-2">
                          <Label className="dark:text-gray-300">{t('stock.lastUpdatedDate')}</Label>
                          <Input
                            type="date"
                            value={editForm.lastUpdated}
                            onChange={(e) => setEditForm({...editForm, lastUpdated: e.target.value})}
                            className="dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                          />
                        </div>
                        <div className="flex gap-2 justify-end">
                          <Button variant="outline" onClick={() => setEditingStock(null)} className="dark:border-gray-600 dark:text-gray-300 dark:hover:bg-gray-600">
                            {t('stock.cancel')}
                          </Button>
                          <Button onClick={handleSaveStock}>
                            {t('stock.updateButton')}
                          </Button>
                        </div>
                      </div>
                    </DialogContent>
                  </Dialog>
                </div>
              </div>
            );
          })}
        </div>

        {/* Stock Summary */}
        <div className="mt-6 pt-4 border-t dark:border-gray-600">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="text-center p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                {stock.find(s => s.batteryType === 'AA')?.quantity || 0}
              </div>
              <div className="text-sm text-blue-600 dark:text-blue-400">{t('stock.aaStock')}</div>
            </div>
            <div className="text-center p-3 bg-green-50 dark:bg-green-900/20 rounded-lg">
              <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                {stock.find(s => s.batteryType === 'AAA')?.quantity || 0}
              </div>
              <div className="text-sm text-green-600 dark:text-green-400">{t('stock.aaaStock')}</div>
            </div>
            <div className="text-center p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
              <div className="text-2xl font-bold text-gray-600 dark:text-gray-300">
                {stock.reduce((sum, s) => sum + s.quantity, 0)}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">{t('stock.totalStock')}</div>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
