import React, { useState } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { BatteryEntry } from '@/app/api/batteries/route';
import { useLanguage } from '@/contexts/LanguageContext';

type BatteryListProps = {
  batteries: BatteryEntry[];
  isLoading?: boolean;
  onUpdate: (updatedEntry: BatteryEntry) => void;
  onDelete: (id: string) => void;
};

export default function BatteryList({ batteries, isLoading, onUpdate, onDelete }: BatteryListProps) {
  const { t } = useLanguage();
  const [editingEntry, setEditingEntry] = useState<BatteryEntry | null>(null);
  const [editForm, setEditForm] = useState({
    batteryType: 'AA' as 'AA' | 'AAA',
    quantity: 0,
    assignedTo: '',
    acquiredDate: ''
  });

  const handleEdit = (entry: BatteryEntry) => {
    setEditingEntry(entry);
    setEditForm({
      batteryType: entry.batteryType,
      quantity: entry.quantity,
      assignedTo: entry.assignedTo,
      acquiredDate: entry.acquiredDate
    });
  };

  const handleSaveEdit = async () => {
    if (!editingEntry) return;

    try {
      const res = await fetch(`/api/batteries?id=${editingEntry.id}`, {
        method: 'PUT',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(editForm)
      });

      if (!res.ok) {
        throw new Error('Failed to update entry');
      }

      const updatedEntry = await res.json();
      onUpdate(updatedEntry);
      setEditingEntry(null);
    } catch (error) {
      console.error('Error updating entry:', error);
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm(t('common.deleteConfirm'))) return;

    try {
      const res = await fetch(`/api/batteries?id=${id}`, {
        method: 'DELETE'
      });

      if (!res.ok) {
        throw new Error('Failed to delete entry');
      }

      onDelete(id);
    } catch (error) {
      console.error('Error deleting entry:', error);
    }
  };

  if (isLoading) {
    return (
      <Card className="dark:bg-gray-800 dark:border-gray-700">
        <CardHeader>
          <CardTitle className="dark:text-white">{t('list.title')}</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center py-8">
            <div className="text-gray-500 dark:text-gray-400">{t('list.loading')}</div>
          </div>
        </CardContent>
      </Card>
    );
  }

  if (batteries.length === 0) {
    return (
      <Card className="dark:bg-gray-800 dark:border-gray-700">
        <CardHeader>
          <CardTitle className="dark:text-white">{t('list.title')}</CardTitle>
        </CardHeader>
        <CardContent>
          <div className="flex items-center justify-center py-8">
            <div className="text-center">
              <div className="text-gray-500 dark:text-gray-400 mb-2">{t('list.noEntries')}</div>
              <div className="text-sm text-gray-400 dark:text-gray-500">{t('list.addFirst')}</div>
            </div>
          </div>
        </CardContent>
      </Card>
    );
  }

  const getBatteryTypeColor = (type: string) => {
    return type === 'AA' ? 'bg-blue-100 text-blue-800 dark:bg-blue-900 dark:text-blue-200' : 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200';
  };

  return (
    <Card className="dark:bg-gray-800 dark:border-gray-700">
      <CardHeader>
        <CardTitle className="dark:text-white">{t('list.title')} ({batteries.length})</CardTitle>
      </CardHeader>
      <CardContent>
        <div className="overflow-x-auto">
          <table className="w-full border-collapse">
            <thead>
              <tr className="border-b dark:border-gray-600">
                <th className="text-left p-3 font-medium dark:text-gray-300">{t('list.type')}</th>
                <th className="text-left p-3 font-medium dark:text-gray-300">{t('list.quantity')}</th>
                <th className="text-left p-3 font-medium dark:text-gray-300">{t('list.assignedTo')}</th>
                <th className="text-left p-3 font-medium dark:text-gray-300">{t('list.date')}</th>
                <th className="text-left p-3 font-medium dark:text-gray-300">{t('list.actions')}</th>
              </tr>
            </thead>
            <tbody>
              {batteries.map((entry) => (
                <tr key={entry.id} className="border-b dark:border-gray-600 hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors">
                  <td className="p-3">
                    <Badge className={getBatteryTypeColor(entry.batteryType)}>
                      {entry.batteryType}
                    </Badge>
                  </td>
                  <td className="p-3">
                    <span className="font-medium dark:text-white">{entry.quantity}</span>
                    <span className="text-gray-500 dark:text-gray-400 ml-1">
                      {entry.quantity === 1 ? t('stock.battery') : t('stock.batteries')}
                    </span>
                  </td>
                  <td className="p-3">
                    <div className="font-medium dark:text-white">{entry.assignedTo}</div>
                  </td>
                  <td className="p-3">
                    <div className="text-gray-600 dark:text-gray-400">
                      {new Date(entry.acquiredDate).toLocaleDateString('tr-TR', {
                        year: 'numeric',
                        month: 'short',
                        day: 'numeric'
                      })}
                    </div>
                  </td>
                  <td className="p-3">
                    <div className="flex gap-2">
                      <Dialog>
                        <DialogTrigger asChild>
                          <Button 
                            variant="outline" 
                            size="sm"
                            onClick={() => handleEdit(entry)}
                            className="dark:border-gray-600 dark:text-gray-300 dark:hover:bg-gray-600"
                          >
                            {t('list.edit')}
                          </Button>
                        </DialogTrigger>
                        <DialogContent className="dark:bg-gray-800 dark:border-gray-700">
                          <DialogHeader>
                            <DialogTitle className="dark:text-white">{t('edit.title')}</DialogTitle>
                          </DialogHeader>
                          <div className="space-y-4">
                            <div className="space-y-2">
                              <Label className="dark:text-gray-300">{t('edit.batteryType')}</Label>
                              <Select 
                                value={editForm.batteryType} 
                                onValueChange={(value) => setEditForm({...editForm, batteryType: value as 'AA' | 'AAA'})}
                              >
                                <SelectTrigger className="dark:bg-gray-700 dark:border-gray-600 dark:text-white">
                                  <SelectValue />
                                </SelectTrigger>
                                <SelectContent className="dark:bg-gray-700 dark:border-gray-600">
                                  <SelectItem value="AA" className="dark:text-white dark:hover:bg-gray-600">AA</SelectItem>
                                  <SelectItem value="AAA" className="dark:text-white dark:hover:bg-gray-600">AAA</SelectItem>
                                </SelectContent>
                              </Select>
                            </div>
                            <div className="space-y-2">
                              <Label className="dark:text-gray-300">{t('edit.quantity')}</Label>
                              <Input
                                type="number"
                                value={editForm.quantity}
                                onChange={(e) => setEditForm({...editForm, quantity: parseInt(e.target.value) || 0})}
                                className="dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                              />
                            </div>
                            <div className="space-y-2">
                              <Label className="dark:text-gray-300">{t('edit.assignedTo')}</Label>
                              <Input
                                value={editForm.assignedTo}
                                onChange={(e) => setEditForm({...editForm, assignedTo: e.target.value})}
                                className="dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                              />
                            </div>
                            <div className="space-y-2">
                              <Label className="dark:text-gray-300">{t('edit.acquiredDate')}</Label>
                              <Input
                                type="date"
                                value={editForm.acquiredDate}
                                onChange={(e) => setEditForm({...editForm, acquiredDate: e.target.value})}
                                className="dark:bg-gray-700 dark:border-gray-600 dark:text-white"
                              />
                            </div>
                            <div className="flex gap-2 justify-end">
                              <Button variant="outline" onClick={() => setEditingEntry(null)} className="dark:border-gray-600 dark:text-gray-300 dark:hover:bg-gray-600">
                                {t('edit.cancel')}
                              </Button>
                              <Button onClick={handleSaveEdit}>
                                {t('edit.save')}
                              </Button>
                            </div>
                          </div>
                        </DialogContent>
                      </Dialog>
                      <Button 
                        variant="destructive" 
                        size="sm"
                        onClick={() => handleDelete(entry.id)}
                      >
                        {t('list.delete')}
                      </Button>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        
        {/* Summary Section */}
        <div className="mt-6 pt-4 border-t dark:border-gray-600">
          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            <div className="text-center p-3 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
              <div className="text-2xl font-bold text-blue-600 dark:text-blue-400">
                {batteries.filter(b => b.batteryType === 'AA').reduce((sum, b) => sum + b.quantity, 0)}
              </div>
              <div className="text-sm text-blue-600 dark:text-blue-400">{t('list.aaBatteries')}</div>
            </div>
            <div className="text-center p-3 bg-green-50 dark:bg-green-900/20 rounded-lg">
              <div className="text-2xl font-bold text-green-600 dark:text-green-400">
                {batteries.filter(b => b.batteryType === 'AAA').reduce((sum, b) => sum + b.quantity, 0)}
              </div>
              <div className="text-sm text-green-600 dark:text-green-400">{t('list.aaaBatteries')}</div>
            </div>
            <div className="text-center p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
              <div className="text-2xl font-bold text-gray-600 dark:text-gray-300">
                {batteries.reduce((sum, b) => sum + b.quantity, 0)}
              </div>
              <div className="text-sm text-gray-600 dark:text-gray-400">{t('list.totalBatteries')}</div>
            </div>
          </div>
        </div>
      </CardContent>
    </Card>
  );
}
