'use client';

import React, { useState } from 'react';
import { useForm } from 'react-hook-form';
import * as z from 'zod';
import { zodResolver } from '@hookform/resolvers/zod';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { BatteryEntry } from '@/app/api/batteries/route';
import { useLanguage } from '@/contexts/LanguageContext';

const batterySchema = z.object({
  batteryType: z.enum(['AA', 'AAA'], {
    required_error: 'Please select a battery type'
  }),
  quantity: z.number().min(1, 'Quantity must be at least 1'),
  assignedTo: z.string().min(1, 'Please enter a name'),
  acquiredDate: z.string().min(1, 'Please provide a date'),
});

type BatteryFormData = z.infer<typeof batterySchema>;

type BatteryFormProps = {
  onAdd: (entry: BatteryEntry) => void;
  onStockUpdate?: () => void;
};

export default function BatteryForm({ onAdd, onStockUpdate }: BatteryFormProps) {
  const { t } = useLanguage();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitError, setSubmitError] = useState<string | null>(null);

  const { 
    register, 
    handleSubmit, 
    reset, 
    setValue,
    formState: { errors } 
  } = useForm<BatteryFormData>({
    resolver: zodResolver(batterySchema),
    defaultValues: {
      acquiredDate: new Date().toISOString().split('T')[0]
    }
  });

  const onSubmit = async (data: BatteryFormData) => {
    setIsSubmitting(true);
    setSubmitError(null);
    
    try {
      // First, try to reduce stock
      const stockRes = await fetch('/api/stock', {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          batteryType: data.batteryType,
          quantity: data.quantity
        })
      });

      if (!stockRes.ok) {
        const stockError = await stockRes.json();
        throw new Error(stockError.error || 'Failed to update stock');
      }

      // If stock update successful, create battery entry
      const res = await fetch('/api/batteries', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(data)
      });

      if (!res.ok) {
        const errorData = await res.json();
        throw new Error(errorData.error || 'Failed to save battery entry');
      }
      
      const savedEntry = await res.json();
      onAdd(savedEntry);
      onStockUpdate?.(); // Notify parent to refresh stock
      reset({
        acquiredDate: new Date().toISOString().split('T')[0]
      });
    } catch (error: any) {
      setSubmitError(error.message);
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <Card className="mb-8 dark:bg-gray-800 dark:border-gray-700">
      <CardHeader>
        <CardTitle className="text-xl font-semibold dark:text-white">{t('assign.title')}</CardTitle>
        <p className="text-sm text-gray-600 dark:text-gray-400">
          {t('assign.description')}
        </p>
      </CardHeader>
      <CardContent>
        <form onSubmit={handleSubmit(onSubmit)} className="space-y-4">
          {submitError && (
            <div className="p-3 text-sm text-red-600 bg-red-50 border border-red-200 rounded-md dark:bg-red-900/20 dark:border-red-800 dark:text-red-400">
              {submitError}
            </div>
          )}

          <div className="space-y-2">
            <Label htmlFor="batteryType" className="dark:text-gray-300">{t('assign.batteryType')}</Label>
            <Select onValueChange={(value) => setValue('batteryType', value as 'AA' | 'AAA')}>
              <SelectTrigger className="dark:bg-gray-700 dark:border-gray-600 dark:text-white">
                <SelectValue placeholder={t('assign.selectType')} />
              </SelectTrigger>
              <SelectContent className="dark:bg-gray-700 dark:border-gray-600">
                <SelectItem value="AA" className="dark:text-white dark:hover:bg-gray-600">AA</SelectItem>
                <SelectItem value="AAA" className="dark:text-white dark:hover:bg-gray-600">AAA</SelectItem>
              </SelectContent>
            </Select>
            {errors.batteryType && (
              <p className="text-sm text-red-600 dark:text-red-400">{errors.batteryType.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="quantity" className="dark:text-gray-300">{t('assign.quantity')}</Label>
            <Input
              id="quantity"
              type="number"
              min="1"
              placeholder={t('assign.enterQuantity')}
              {...register('quantity', { valueAsNumber: true })}
              className="dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:placeholder-gray-400"
            />
            {errors.quantity && (
              <p className="text-sm text-red-600 dark:text-red-400">{errors.quantity.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="assignedTo" className="dark:text-gray-300">{t('assign.assignedTo')}</Label>
            <Input
              id="assignedTo"
              type="text"
              placeholder={t('assign.enterName')}
              {...register('assignedTo')}
              className="dark:bg-gray-700 dark:border-gray-600 dark:text-white dark:placeholder-gray-400"
            />
            {errors.assignedTo && (
              <p className="text-sm text-red-600 dark:text-red-400">{errors.assignedTo.message}</p>
            )}
          </div>

          <div className="space-y-2">
            <Label htmlFor="acquiredDate" className="dark:text-gray-300">{t('assign.assignmentDate')}</Label>
            <Input
              id="acquiredDate"
              type="date"
              {...register('acquiredDate')}
              className="dark:bg-gray-700 dark:border-gray-600 dark:text-white"
            />
            {errors.acquiredDate && (
              <p className="text-sm text-red-600 dark:text-red-400">{errors.acquiredDate.message}</p>
            )}
          </div>

          <Button 
            type="submit" 
            className="w-full" 
            disabled={isSubmitting}
          >
            {isSubmitting ? t('assign.assigning') : t('assign.assignButton')}
          </Button>
        </form>
      </CardContent>
    </Card>
  );
}
