'use client';

import React, { useEffect, useState } from 'react';
import Image from 'next/image';
import BatteryForm from '@/components/BatteryForm';
import BatteryList from '@/components/BatteryList';
import StockManager from '@/components/StockManager';
import Header from '@/components/Header';
import { BatteryEntry } from '@/app/api/batteries/route';
import { useLanguage } from '@/contexts/LanguageContext';

export default function DashboardPage() {
  const { t } = useLanguage();
  const [batteries, setBatteries] = useState<BatteryEntry[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [stockUpdateTrigger, setStockUpdateTrigger] = useState(0);

  // Fetch battery stock data from API on mount
  useEffect(() => {
    async function fetchBatteries() {
      try {
        setIsLoading(true);
        const res = await fetch('/api/batteries');
        if (!res.ok) {
          throw new Error('Failed to fetch battery stocks');
        }
        const data = await res.json();
        setBatteries(data);
        setError(null);
      } catch (err: any) {
        setError(err.message);
        console.error('Error fetching batteries:', err);
      } finally {
        setIsLoading(false);
      }
    }
    
    fetchBatteries();
  }, []);

  // Handler to update battery list on new entry submission
  const addBatteryEntry = (entry: BatteryEntry) => {
    setBatteries(prev => [...prev, entry]);
  };

  // Handler to update an existing battery entry
  const updateBatteryEntry = (updatedEntry: BatteryEntry) => {
    setBatteries(prev => 
      prev.map(entry => 
        entry.id === updatedEntry.id ? updatedEntry : entry
      )
    );
  };

  // Handler to delete a battery entry
  const deleteBatteryEntry = (id: string) => {
    setBatteries(prev => prev.filter(entry => entry.id !== id));
  };

  // Handler to trigger stock refresh
  const handleStockUpdate = () => {
    setStockUpdateTrigger(prev => prev + 1);
  };

  return (
    <div className="min-h-screen bg-gray-50 dark:bg-gray-900 transition-colors">
      <main className="container mx-auto px-4 py-8 max-w-7xl">
        {/* Header with Language and Theme Toggle */}
        <Header />

        {/* Header Section */}
        <header className="mb-8">
          <div className="relative w-full h-48 rounded-lg overflow-hidden shadow-lg mb-6">
            <Image
              src="https://images.pexels.com/photos/414612/pexels-photo-414612.jpeg"
              alt="Battery stocks banner"
              fill
              className="object-cover"
              priority
            />
            <div className="absolute inset-0 bg-black bg-opacity-40 flex items-center justify-center">
              <div className="text-center text-white">
                <h1 className="text-4xl font-bold mb-2">{t('app.title')}</h1>
                <p className="text-lg opacity-90">{t('app.subtitle')}</p>
              </div>
            </div>
          </div>
        </header>

        {/* Error Display */}
        {error && (
          <div className="mb-6 p-4 text-red-600 bg-red-50 border border-red-200 rounded-lg dark:bg-red-900/20 dark:border-red-800 dark:text-red-400">
            <div className="font-medium">{t('common.error')}</div>
            <div className="text-sm mt-1">{error}</div>
          </div>
        )}

        {/* Stock Management Section */}
        <div className="mb-8">
          <StockManager key={stockUpdateTrigger} onStockUpdate={handleStockUpdate} />
        </div>

        {/* Main Content Grid */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
          {/* Form Section */}
          <div className="lg:col-span-1">
            <BatteryForm onAdd={addBatteryEntry} onStockUpdate={handleStockUpdate} />
          </div>

          {/* List Section */}
          <div className="lg:col-span-2">
            <BatteryList 
              batteries={batteries} 
              isLoading={isLoading}
              onUpdate={updateBatteryEntry}
              onDelete={deleteBatteryEntry}
            />
          </div>
        </div>

        {/* Footer */}
        <footer className="mt-12 pt-8 border-t border-gray-200 dark:border-gray-700 text-center text-gray-500 dark:text-gray-400">
          <p className="text-sm">
            {t('footer.text')}
          </p>
        </footer>
      </main>
    </div>
  );
}
